
import { Product } from "@/types";
import { defineComponent, PropType } from "vue";
import { useCartStore } from "@/stores/cart";

export default defineComponent({
  props: {
    product: {
      type: Object as PropType<Product>,
      required: true,
    },
    quantity: {
      type: Number,
      default: 1,
    },
    updateQuantity: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "small",
    },
    text: {
      type: String,
      default: "",
    },
  },
  setup() {
    const cartStore = useCartStore();

    return { cartStore };
  },
});
