
import { defineComponent } from "vue";
import { useWishlistStore } from "@/stores/wishlist";

export default defineComponent({
  props: {
    productId: {
      type: Number,
      required: true,
    },
    size: {
      type: String,
      default: "small",
    },
  },
  setup() {
    const wishlistStore = useWishlistStore();
    return { wishlistStore };
  },
});
