
import { defineComponent } from "vue";
import { useProductsList } from "@/hooks/useProductsList";
import ProductsList from "@/components/ProductsList.vue";
import ProductsFilter from "@/components/ProductsFilter.vue";

export default defineComponent({
  components: { ProductsList, ProductsFilter },
  setup() {
    const {
      products,
      isLoadingProducts,
      currentPage,
      totalPages,
      sortingElements,
      filteringElements,
      searchQuery,
      selectedSorting,
      selectedFilters,
    } = useProductsList();

    return {
      products,
      isLoadingProducts,
      searchQuery,
      sortingElements,
      selectedSorting,
      selectedFilters,
      filteringElements,
      currentPage,
      totalPages,
    };
  },
});
